body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
        Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color:#fafafa;
    
    line-height: 1.5;
}

img{
    display: block;
    width: 100%;
    height: auto;
    object-fit: cover

}
.gallery{
    padding: 40px;
    display: flex;
    flex-wrap: wrap;
}

.img-container{
    width: 260px;
    height: 175px;
}
.img-container > img{
    display: block;
    height: 100%;
    width: 100%;
    object-fit: cover;
}
.photo-card{
    margin: 10px;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
}
.info-item{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 5px;
}
.info{
    display: flex;
    align-items: center;
    padding-left: 3px;
  
}
.text{
    font-size: 14px;
}

.property{
    font-weight: bold;
}
.value{
    font-weight: normal;
}
